<template>
  <div class="bonusgame" :class="langText==''?'':'English'">
    <div  class="luange flex flexjcsb" @click="changeLocale()">
      <div class="logo flex flexac">
          <!-- Logo -->
          <img src="../../static/images/Logo-2.gif">
      </div>
      <div class="flex flexac">
        <img v-if="langText==''" src="../../static/images/zhongwen.png" />
        <img v-if="langText=='English'" src="../../static/images/yinwen.png" />
        <img src="../../static/images/drop_down.png" />
      </div>
    </div>
    <div v-if="isInjected==false" class="appinstall flex flexjcsb flexac">
      <div class="flex flexac">
        <div class="applogo">
          <img src="../../static/images/eth_plug.png" />
        </div>
        <div class="installInfo">
          <div class="flex flexjcfs">MetaMask</div>
          <div class="flex flexjcfs">{{ $t('top.downTip') }}</div>
        </div>
      </div>
      <div class="anzhuang flexcenter" @click="installMetaMask()">{{ $t('top.downBtn') }}</div>
    </div>
    <!-- <div class="pageTitle flexcenter">{{ $t('top.pageTitle') }}</div> -->
    <div class="wrap">
      <div class="banner">
        <div class="img">
           <img v-if="langText==''" src="../../static/images/bannerEn.png">
           <img v-if="langText=='English'" src="../../static/images/banner.png">
        </div>
      </div>
      <div class="figure conMt">
        <div class="tr flex">
          <div class="box flex1">
            <div class="line flex">
              <span>{{ $t('info1.inviteCode') }}</span>
              <span
                class="copyCodeText fuzhi"
                :data-clipboard-text="inviteUrl"
                @click="copyCode()"
              >{{ $t('info1.copyCode') }}</span>
            </div>
            <div class="line flex yaoqingma">{{invest.inviteCode}}</div>
          </div>
          <div class="box flex1">
            <div class="line flex">{{ $t('info1.nodeNumber') }}</div>
            <div class="line flex jieshu">{{gameInfo.pid }}</div>
          </div>
          <div class="box flex1" @click="nodeApply">
            <div class="line flex">{{ $t('info1.snodeNumber') }}
              <img  class="isnode"  src="../../static/images/isnode.png">
              <!-- <img v-if="" class="isnode" src="../../static/images/nonode.png"> -->
            </div>
            <div class="line flex jieshu">0</div>
          </div>
        </div>
        <div class="tr flex">
          <div class="box flex1">
            <div class="line flex">{{ $t('info1.level') }}</div>
            <div class="line flex">
              <span class="dengji">{{player.level}}</span>
              <span class="danwei">star</span>
            </div>
          </div>
          <div class="box flex1">
            <div class="line flex">{{ $t('info1.nodeLevel') }}</div>
            <div class="line flex">
              <span class="dengji">{{player.nodeLevel}}</span>
              <span class="danwei">star</span>
            </div>
          </div>
          <div class="box flex1">
            <div class="line flex">{{ $t('info1.investAmount') }}</div>
            <div class="line flex">
              <span class="dengji yue">{{invest.amount}}</span>
              <span class="danwei">Eth</span>
            </div>
          </div>
        </div>
      </div>
      <div class="overbooking conMt">
        <div class="thead flex flexjcsb flexac">
          <div class="left flex flexac">
            <div class="conIcon">
              <img class="icon titleIcon" src="../../static/images/eth_logo.png" />
            </div>
            {{ $t('info2.ethbalance') }}
          </div>
          <div class="right">
            <span style="margin-right:0.5rem;font-size:1.6rem">{{ balance | numFilter }}</span>ETH
          </div>
        </div>
        <div class="flex flexjcsb conyazhu">
          <div class="yazhuBtn flex">
            <div class="jian flexcenter" @click="setValue('down')">-</div>
            <div class="val">
              <input :value="ethVal+' ETH'" disabled="disabled" />
            </div>
            <div class="jia flexcenter" @click="setValue('up')">+</div>
          </div>
          <div class="touzhuBtn flexcenter" @click="investGame()">{{ $t('info2.betting') }}</div>
        </div>
        <div class="btngroup flex">
          <div class="btn flexcenter flex1">
            <div class="con" @click="setEthValue(1)">1 ETH</div>
          </div>
          <div class="btn flexcenter flex1">
            <div class="con" @click="setEthValue(6)">6 ETH</div>
          </div>
          <div class="btn flexcenter flex1">
            <div class="con" @click="setEthValue(11)">11 ETH</div>
          </div>
          <div class="btn flexcenter flex1">
            <div class="con" @click="setEthValue(15)">15 ETH</div>
          </div>
        </div>
      </div>
      <div class="settlement conMt">
        <div class="title flex flexac">
          <div class="conIcon">
            <img class="icon titleIcon" src="../../static/images/profit.png" />
          </div>
          {{ $t('info3.settleRecord') }}
        </div>
        <div class="mainCon">
          <div class="line flex flexjcsb">
            <div class="left flex flexjcfs">{{ $t('info3.dateTime') }}</div>
            <div class="right flex flexjcfe">{{ $t('info3.profit') }}</div>
          </div>
          <div class="line flex flexjcsb">
            <div class="left flex flexjcfs">2019-11-16</div>
            <div class="right flex flexjcfe">{{ player.dayBonusAmount }} ETH</div>
          </div>
          <div class="line flex flexjcsb">
            <div class="left flex flexjcfs">2019-11-16</div>
            <div class="right flex flexjcfe">{{ player.inviteAmonut }} ETH</div>
          </div>
        </div>
      </div>
      <div class="capital conMt">
        <div class="title flex flexjcsb brb">
          <div class="flex flexac">
            <div class="conIcon">
              <img class="icon titleIcon" src="../../static/images/balance.png" />
            </div>
            <div class="text">{{ $t('info4.freeAmount') }}</div>
            <div>
              <span class="ziben">{{player.freeAmount}}</span>
              <span>ETH</span>
            </div>
          </div>
          <div class="tixian flexcenter" @click="withDrawGame()">{{ $t('info4.withdraw') }}</div>
        </div>
        <div class="mainCon">
          <div class="tr flex">
            <div class="box flex1">
              <div class="title flex flexjcfs">{{ $t('info4.rechargeAmount') }}</div>
              <div class="text flex flexjcfs">{{ $t('info4.tip1') }}</div>
              <div class="num flex flexjcfs">{{player.rechargeAmount}} ETH</div>
            </div>
            <div class="box flex1">
              <div class="title flex flexjcfs">{{ $t('info4.withdrawAmount') }}</div>
              <div class="text flex flexjcfs zongshouruTip">{{ $t('info4.tip2') }}</div>
              <div class="num flex flexjcfs">{{player.withdrawAmount}} ETH</div>
            </div>
          </div>
          <div class="tr flex">
            <div class="box flex1">
              <div class="title flex flexjcfs">{{ $t('info4.bonusAmount') }}</div>
              <div class="text flex flexjcfs">{{ $t('info4.tip3') }}</div>
              <div class="num flex flexjcfs">{{player.bonusAmount}} ETH</div>
            </div>
            <div class="box flex1">
              <div class="title flex flexjcfs">{{ $t('info4.inviteAmonut') }}</div>
              <div class="text flex flexjcfs">{{ $t('info4.tip4') }}</div>
              <div class="num flex flexjcfs">{{player.inviteAmonut}} ETH</div>
            </div>
          </div>
        </div>
      </div>

      <div class="footer">
        <div class="tr flex">
          <div @click="gamerules" class="flex1 flex colortex flextc">{{ $t('footer.tip1') }}</div>
          <div class="flex1 flex colortex flextc">
            <a class="colortex" target="_blank"  href="https://www.dapp.com">{{ $t('footer.tip2') }}</a>
          </div>
          <div class="flex1 flex colortex flextc">
             <a class="colortex" target="_blank" href="https://cdp-cn.makerdao.com">{{ $t('footer.tip3') }}</a>
          </div>
          <div class="flex1 flex colortex flextc">
             <a class="colortex" target="_blank"  href="https://ethereum.org">{{ $t('footer.tip4') }}</a>
          </div>
        </div>
      </div>


    </div>

    <div class="isInjectedFalse" v-if="modelView" @touchmove.prevent>
      <div class="modelView" @click="closeModel"></div>
      <div class="appinstall flex flexjcsb flexac">
        <div class="flex flexac">
          <div class="applogo">
            <img src="../../static/images/eth_plug.png" />
          </div>
          <div class="installInfo">
            <div class="flex flexjcfs">MetaMask</div>
            <div class="flex flexjcfs">{{ $t('top.downTip') }}</div>
          </div>
        </div>
        <div class="anzhuang flexcenter" @click="installMetaMask()">{{ $t('top.downBtn') }}</div>
      </div>
    </div>

    <div class="gameModel" v-if="gameModel" >
        <div class="modelView" @click="closeModel"></div>
        <div class="content">
            <div class="title flexcenter">
              
              <span class="iconfont icon-guanbi flexcenter" @click="closeModel"></span>
            </div>
            <div class="textLine textLine1">
                {{ $t('rules.tip1') }}
            </div>
              <div class="textLine textLine2">
                {{ $t('rules.tip2') }}
            </div>
              <div class="textLine textLine3">
                {{ $t('rules.tip3') }}
            </div>
              <div class="textLine textLine4">
                {{ $t('rules.tip4') }}
            </div>
             <div class="textLine textLine5">
                {{ $t('rules.tip5') }}
            </div>
             <div class="textLine textLine6">
                {{ $t('rules.tip6') }}
            </div>
             <div class="textLine textLine7">
                {{ $t('rules.tip7') }}
            </div>
             <div class="textLine textLine8">
                {{ $t('rules.tip8') }}
            </div>
             <div class="textLine textLine9">
                {{ $t('rules.tip9') }}
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { NETWORKS } from "../util/Networks.js";
import { mapState } from "vuex";
import { ethers } from "ethers";
import Clipboard from "clipboard";
import Providers from "../util/Providers.js";
import { MessageBox } from "mint-ui";
const random = require("string-random");
export default {
  name: "",
  data() {
    return {
      ethVal: "15",
      langText: "",
      inviteUrl: "",
      isInjected: false,
      modelView: true,
      player: {
        plyAddress: null,
        freeAmount: 0,
        freezeAmount: 0,
        rechargeAmount: 0,
        withdrawAmount: 0,
        inviteAmonut: 0,
        bonusAmount: 0,
        dayInviteAmonut: 0,
        dayBonusAmount: 0,
        level: 0,
        nodeLevel: 0,
        isSuperNode: false
      },
      playerRound: {
        rid: 0,
        start: 0,
        end: 0,
        isVaild: false
      },
      invest: {
        plyAddress: null,
        amount: 0,
        regTime: 0,
        status: 0,
        inviteCode: "-",
        beInvitedCode: "-"
      },
      gameInfo: {
        pid: 0,
        totalCount: 0,
        totalMoney: 0
      },
      gameModel:false
    };
  },
  computed: mapState({
    network: state => NETWORKS[state.web3.networkId],
    coinbase: state => state.web3.coinbase,
    balance: state => state.web3.balance
  }),
  async beforeCreate() {
    console.log("registerEthers Action dispatched from casino-dapp.vue");
    await this.$store.dispatch("registerEthers");
  },
  components: {},
  created() {
    console.log("2321421sdada");
    console.log(this.$i18n.locale);
    this.$i18n.locale == "en-US"
      ? (this.langText = "English")
      : (this.langText = "");
  },
  async mounted() {
    console.log("dispatching getContractInstance");
    await this.$store.dispatch("getContractInstance");
    setInterval(() => {
      if (this.coinbase != null) this.getContractInfo();
    }, 1000 * 5);
    if (Providers.isMetaMaskProvider()) {
      this.isInjected = true;
      this.modelView=false;

    }
  },
  filters: {
    numFilter(value) {
      let realVal = "";
      if (value) {
        realVal = parseFloat(value).toFixed(4);
      } else {
        realVal = "0";
      }
      return realVal;
    }
  },
  methods: {
    gamerules:function(){
      this.gameModel=true;
    },
       nodeApply: function() {
         console.log(this.$i18n.locale)
         if(this.$i18n.locale=='zh-CN'){
            MessageBox({
              title: "?",
              message: "20ETH",
              showCancelButton: true,
            }).then((res)=>{
              console.log(res)
            })
         }else{
            MessageBox({
              title: "Do you want to be the creation node?",
              message: "You must consume 20eth from your wallet balance. Please make a choice!",
              showCancelButton: true,
              confirmButtonText:'define',
              cancelButtonText:'cancel'
            }).then((res)=>{
              console.log(res)
            })
         }
   
    
    },
    closeModel(){
      this.modelView=false,
      this.gameModel=false
    },
    changeLocale() {
      this.lang = this.$i18n.locale;
      console.log(this.lang);
      if (this.lang === "zh-CN") {
        this.lang = "en-US";
        this.$i18n.locale = this.lang;
        this.langText = "";
      } else {
        this.lang = "zh-CN";
        this.$i18n.locale = this.lang;
        this.langText = "English";
      }
      localStorage.setItem("locale", this.lang);
    },
    setEthValue: function(val) {
      this.ethVal = val;
    },
    setValue: function(type) {
      var ethVal = parseInt(this.ethVal);
      if (type == "down") {
        ethVal > 0 ? (this.ethVal = ethVal - 1) : (this.ethVal = this.ethVal);
      } else if (type == "up") {
        this.ethVal = ethVal + 1;
      }
    },
    copyCode: function() {
      let _this = this;
      if (this.inviteUrl == null || this.inviteUrl == "")
        _this.$toast("");
      let clip = new Clipboard(".copyCodeText");
      clip.on("success", function() {
        _this.$toast("");
      });
      clip.on("error", function() {
        _this.$toast("");
      });
    },
    investGame: function() {
      let _this = this;
      if (this.balance < this.ethVal) {
        _this.$toast("");
        return;
      }
      let inviteCode = random(6);
      let beInvitedCode = _this.invest.regTime!=0?_this.invest.beInvitedCode:this.$route.query.code;
      console.log("beInvitedCode " + beInvitedCode);
      if (beInvitedCode == null || beInvitedCode == "") {
        _this.$toast("");
        return;
      }
      this.$store.state
        .contractInstance()
        .isCodeUsed(inviteCode)
        .then(state => {
          console.log("inviteCode  " + state);
          if (state) {
            _this.$toast("");
          } else {
            this.$store.state
              .contractInstance()
              .isCodeUsed(beInvitedCode)
              .then(state => {
                if (state) {
                  console.log("beInvitedCode  " + state);
                  let overrides = {
                    gasLimit: 5000000,
                    value: ethers.utils.parseEther("" + this.ethVal)
                  };
                  this.$store.state
                    .contractInstance()
                    .investGame(inviteCode, beInvitedCode, overrides)
                    .then(tx => {
                      _this.$toast("! ID:" + tx.hash);
                      console.log("investGame  " + tx.hash);
                    });
                } else {
                  _this.$toast("");
                }
              });
          }
        });
    },
    withDrawGame: function() {
      let _this = this;
      if (_this.player.freeAmount <= 0.1) {
        _this.$toast("0.1ETH");
        return;
      }
      let overrides = {
        gasLimit: 5000000
      };
      this.$store.state
        .contractInstance()
        .withDraw(overrides)
        .then(tx => {
          _this.$toast("! ID:" + tx.hash);
          console.log("withDraw  " + tx.hash);
        });
    },
 
    getPlayerInfoPlyAdd: function() {
      let _this = this;
      if (this.coinbase == null) return;
      this.$store.state
        .contractInstance()
        .getPlayerInfoPlyAdd(this.coinbase)
        .then(info => {
          console.log("getPlayerInfoPlyAdd  " + info);
          (_this.player.plyAddress = info[0]),
            (_this.player.freeAmount =
              info[1] == 0 ? 0 : ethers.utils.formatEther("" + info[1])),
            (_this.player.freezeAmount =
              info[2] == 0 ? 0 : ethers.utils.formatEther("" + info[2])),
            (_this.player.rechargeAmount =
              info[3] == 0 ? 0 : ethers.utils.formatEther("" + info[3])),
            (_this.player.withdrawAmount =
              info[4] == 0 ? 0 : ethers.utils.formatEther("" + info[4])),
            (_this.player.inviteAmonut =
              info[5] == 0 ? 0 : ethers.utils.formatEther("" + info[5])),
            (_this.player.bonusAmount =
              info[6] == 0 ? 0 : ethers.utils.formatEther("" + info[6])),
            (_this.player.dayInviteAmonut =
              info[7] == 0 ? 0 : ethers.utils.formatEther("" + info[7])),
            (_this.player.dayBonusAmount =
              info[8] == 0 ? 0 : ethers.utils.formatEther("" + info[8])),
            (_this.player.level = "" + info[9]),
            (_this.player.nodeLevel = "" + info[10]),
            (_this.player.isSuperNode = info[11]);
        });
    },
    getInvestInfoPlyAdd: function() {
      let _this = this;
      if (this.coinbase == null) return;
      this.$store.state
        .contractInstance()
        .getInvestInfoPlyAdd(this.coinbase)
        .then(info => {
          console.log("getInvestInfoPlyAdd  " + info);
          console.log(
            "getInvestInfoPlyAdd +++++++++++++++++++ " + this.isInjected
          );
          console.log(
            "getInvestInfoPlyAdd +++++++++++++++++++ " + this.coinbase
          );
          (_this.invest.amount =
            info[0] == 0 ? 0 : ethers.utils.formatEther("" + info[0])),
            (_this.invest.regTime = info[1]),
            (_this.invest.status = "" + info[2]),
            (_this.invest.inviteCode = info[3]),
            (_this.invest.beInvitedCode = info[4]);
          this.inviteUrl =
            "https://www.btcrecover.com/#/?code=" + _this.invest.inviteCode;
        });
    },
    getGameInfo: function() {
      let _this = this;
      this.$store.state
        .contractInstance()
        .getGameInfo()
        .then(info => {
          console.log("getGameInfo  " + info);
          (_this.gameInfo.pid = "" + info[0]),
            (_this.gameInfo.totalCount =
              info[1] == 0 ? 0 : ethers.utils.formatEther("" + info[1])),
            (_this.gameInfo.totalMoney =
              info[2] == 0 ? 0 : ethers.utils.formatEther("" + info[2]));
        });
    },

    getContractInfo: function() {
      if (this.$store.state.contractInstance() == null) return;
      this.getGameInfo();
      this.getInvestInfoPlyAdd();
      this.getPlayerInfoPlyAdd();
    },
    isCodeUsed: function(code) {
      this.code = this.$route.query.code;
      console.log("code " + code);
    },
    installMetaMask: function() {
      window.location.href = "https://metamask.io/";
    }
  }
};
</script>
<style scoped>
@import url(../../static/css/common.css);
</style>



